"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { NAV } from "@/lib/nav";
import { ChevronDoubleLeftIcon } from "@heroicons/react/24/outline";

type Props = {
  collapsed: boolean;
  onToggleCollapsed: () => void;
  onNavigate?: () => void;
};

const BLUE = "#1877F2";

export default function Sidebar({
  collapsed,
  onToggleCollapsed,
  onNavigate,
}: Props) {
  const pathname = usePathname();

  return (
    <aside
      className={[
        "h-full bg-white border-r border-slate-200",
        "transition-all duration-200 ease-out",
        collapsed ? "w-16" : "w-64",
      ].join(" ")}
    >
      {/* Brand */}
      <div className="h-14 px-3 flex items-center justify-between">
        <Link
          href="/"
          onClick={onNavigate}
          className="flex items-center gap-3 min-w-0"
        >
          <div
            className="h-9 w-9 rounded-xl flex items-center justify-center text-white font-extrabold shadow-sm"
            style={{ backgroundColor: BLUE }}
          >
            DV
          </div>

          {!collapsed && (
            <span className="text-sm font-extrabold text-slate-900 truncate">
              Doc Versioning
            </span>
          )}
        </Link>

        <button
          onClick={onToggleCollapsed}
          className="hidden md:inline-flex h-8 w-8 items-center justify-center rounded-lg hover:bg-slate-100"
        >
          <ChevronDoubleLeftIcon
            className={[
              "h-4 w-4 text-slate-500 transition-transform",
              collapsed && "rotate-180",
            ].join(" ")}
          />
        </button>
      </div>

      {/* Nav */}
      <nav className="mt-4 px-2 space-y-1">
        {NAV.map((item) => {
          const active =
            pathname === item.href ||
            (item.href !== "/" && pathname.startsWith(item.href));
          const Icon = item.icon;

          return (
            <Link
              key={item.href}
              href={item.href}
              onClick={onNavigate}
              title={collapsed ? item.label : undefined}
              className={[
                "group flex items-center gap-3 h-10 px-3 rounded-xl",
                "text-sm font-semibold transition-colors",
                active
                  ? "bg-blue-50 text-blue-600"
                  : "text-slate-600 hover:bg-slate-100",
              ].join(" ")}
            >
              <Icon
                className="h-5 w-5 shrink-0"
                style={{ color: active ? BLUE : "#64748B" }}
              />
              {!collapsed && <span>{item.label}</span>}
            </Link>
          );
        })}
      </nav>

      {/* Footer */}
      {!collapsed && (
        <div className="mt-auto p-3">
          <div className="rounded-xl border border-slate-200 bg-slate-50 p-3 text-xs text-slate-600">
            Tip: อัปโหลดเอกสาร 2 เวอร์ชันแล้วกด{" "}
            <span className="font-bold text-blue-600">Compare</span>
          </div>
        </div>
      )}
    </aside>
  );
}
