"use client";

import React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  Bars3Icon,
  ChevronDoubleLeftIcon,
  ChevronDoubleRightIcon,
  ClockIcon,
  DocumentMagnifyingGlassIcon,
} from "@heroicons/react/24/outline";

type TopbarProps = {
  collapsed: boolean;
  onToggleSidebar: () => void;
  onOpenMobile: () => void;
};

const FB_BLUE = "#1877F2";
const FB_BLUE_HOVER = "#166FE5";

function getTitle(pathname: string) {
  if (pathname === "/") return "Compare Documents";
  if (pathname === "/dashboard") return "Dashboard";
  if (pathname === "/history") return "History";
  if (pathname.startsWith("/compare/")) return "Compare Detail";
  if (pathname === "/documents") return "Documents";
  if (pathname.startsWith("/reports/generate")) return "Reports";
  return "Dashboard";
}

export default function Topbar({
  collapsed,
  onToggleSidebar,
  onOpenMobile,
}: TopbarProps) {
  const pathname = usePathname();
  const title = getTitle(pathname);

  return (
    <header className="sticky top-0 z-40 bg-white/90 backdrop-blur border-b border-blue-100">
      <div className="h-14 px-4 md:px-6 flex items-center justify-between">
        {/* Left */}
        <div className="flex items-center gap-2">
          <button
            onClick={onOpenMobile}
            className="md:hidden inline-flex h-9 w-9 items-center justify-center rounded-lg border border-blue-100 hover:bg-blue-50"
          >
            <Bars3Icon className="h-5 w-5" style={{ color: FB_BLUE }} />
          </button>

          <button
            onClick={onToggleSidebar}
            className="hidden md:inline-flex h-9 items-center gap-2 rounded-lg px-3 border border-blue-100 hover:bg-blue-50"
          >
            {collapsed ? (
              <>
                <ChevronDoubleRightIcon
                  className="h-4 w-4"
                  style={{ color: FB_BLUE }}
                />
                <span
                  className="text-sm font-extrabold"
                  style={{ color: FB_BLUE }}
                >
                  Expand
                </span>
              </>
            ) : (
              <>
                <ChevronDoubleLeftIcon
                  className="h-4 w-4"
                  style={{ color: FB_BLUE }}
                />
                <span
                  className="text-sm font-extrabold"
                  style={{ color: FB_BLUE }}
                >
                  Collapse
                </span>
              </>
            )}
          </button>

          <div className="ml-1 text-sm md:text-base font-extrabold text-slate-900">
            {title}
          </div>
        </div>

        {/* Right */}
        <div className="flex items-center gap-2">
          {pathname !== "/" && (
            <Link
              href="/"
              className="inline-flex items-center gap-2 rounded-lg px-3 py-2 text-sm font-extrabold text-white shadow-sm"
              style={{ backgroundColor: FB_BLUE }}
              onMouseEnter={(e) => {
                e.currentTarget.style.backgroundColor =
                  FB_BLUE_HOVER;
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.backgroundColor = FB_BLUE;
              }}
            >
              <DocumentMagnifyingGlassIcon className="h-4 w-4" />
              Compare
            </Link>
          )}

          <Link
            href="/history"
            className="hidden sm:inline-flex items-center gap-2 rounded-lg border border-blue-100 bg-white px-3 py-2 text-sm font-extrabold hover:bg-blue-50"
            style={{ color: FB_BLUE }}
          >
            <ClockIcon className="h-4 w-4" />
            History
          </Link>
        </div>
      </div>
    </header>
  );
}
